.wrapper{
    width: 100%;
    background-color: #fff;
    @media (min-width: 1270px){
        margin: 0 auto;
        padding: 0 50px;
        width: 1270px;
    }
    @media (min-width: 992px){
        box-shadow: @main-shadow;
    }
    .container{
        padding-left: 0;
        padding-right: 0;
        @media (max-width: 767px){
            padding-left: 15px;
            padding-right: 15px;
        }
    }
    &-header{
        padding: 30px 0;
        background: #001f33;
        border-bottom: 3px solid #63c1c5;
        @media (max-width: 1200px){        
            padding: 20px 0;
            @media (max-width: 992px){
                box-shadow: @main-shadow;
                position: relative;
            }
        }
    }
}

.error-body{
    .flex-display(flex);
    .align-items(center);
    .justify-content(center);
    background-color: #fff;
    
    .error-page{
        padding: 0 15px;
        text-align: center;
        &-logo{
            img{
                @media (max-width: 767px){
                    width: 250px;
                }
            }
        }
        &-number{
            color: #fff;
            font-size: 150px;
            background-color: #001f33;
            width: 100vw;
            padding: 40px 0;
            margin: 40px 0 45px -15px;
            @media (max-width: 1140px){
                font-size: 100px;
                padding: 20px 0;
                margin: 30px 0 35px -15px;
                @media (max-width: 767px){
                    font-size: 70px;
                    padding: 15px 0;
                    margin: 20px 0 25px -15px;
                }
            }
        }
        &-text{
            font-size: 50px;
            color: #001f33;
            width: 550px;
            margin: 0 auto 50px;
            line-height: 1.3;
            @media (max-width: 1140px){
                font-size: 40px;
                margin-bottom: 40px;
                @media (max-width: 767px){
                    font-size: 30px;
                    margin-bottom: 30px;
                    width: auto;
                }
            }
        }
        .btn-def{
            text-transform: uppercase;
            font-size: 20px;
            width: 50%;
            max-width: 350px;
        }
    }
}

.main{
    padding: 30px 0 0;
    @media (max-width: 767px){
        padding: 15px 0 0;
    }

    &.not-front{
        padding: 30px 0;
        .client-form{
            margin-bottom: 10px;
        }
        @media (max-width: 767px){
            padding: 15px 0;
        }
    }

    #map{
        width: 100%;
        height: 400px;
        @media (max-width: 1200px){
            height: 350px;
            @media (max-width: 767px){
                width: 100vw;
            }
        }
        .gmnoprint a,
        .gmnoprint span,
        .gm-style-cc,
        .gm-style-iw + div{
            display: none;
        }
        .contacts-tooltip{
            text-align: center;
            padding: 10px;
            color: #001f33;
            padding: 5px 0 5px 20px;
            &-title{
                font-size: 22px;
            }
        }
    }

    .banners{
        .row{
            @media (min-width: 767px){
                display: flex;
            }
        }
        margin-bottom: 40px;
        @media (max-width: 1200px){
            margin-bottom: 30px;
            @media (max-width: 767px){
                margin-bottom: 20px;
            }
        }
        .banner-wrapper{
            @media (max-width: 767px){
                margin-bottom: 0;                
            }
        }
        .banner{
            color: #fff;
            padding: 215px 20px 20px;
            position: relative;
            transition: .3s;
            height: 100%; 
            color: #fff;
            display: block;
            @media (max-width: 767px){
                background-size: 104px 105px !important;
                padding: 145px 10px 15px;
                @media (max-width: 480px){
                    padding: 130px 10px 15px;
                }
            }
            &-title{
                h2{
                    font-size: 26px;
                    text-align: center;
                    line-height: 1.3;
                    margin: 0;
                    @media (max-width: 1200px){
                        font-size: 22px;
                        @media (max-width: 992px){
                            line-height: 1.2;
                            @media (max-width: 480px){
                                font-size: 18px;
                            }
                        }
                    }
                }
            }
            &.blue{
                background: #001f33 url('../img/co2.png') no-repeat 50% 10%;
            }
            &.sky{
                background: #63c1c5 url('../img/water.png') no-repeat 50% 10%;
            }
            &.green{
                background: #67b168 url('../img/polution.png') no-repeat 50% 10%;
            }
            &:hover{
                animation-name: bg;
                animation-duration: 0.5s;
                animation-timing-function: ease;
                h2{
                    text-decoration: underline;
                }
            }
        }
    }

    .front-text{
        color: #001f33;
        margin: 70px 0;
        background: #f7f7f7;
        @media (max-width: 1200px){
            margin: 50px 0;
        }
        &-title{
            margin: -30px auto 20px;
            font-size: 26px;
            font-weight: 700;
            width: max-content;
            padding: 15px 30px;
            background: #63c1c5;
            text-transform: uppercase;
            color: #fff;
            @media (max-width: 992px){
                font-size: 22px;
                margin: -30px auto 10px;
                @media (max-width: 767px){
                    font-size: 20px;
                }
            }
        }
        &-desc{
            margin-bottom: 15px;
            padding: 0 15px;
            @media (max-width: 992px){
                font-size: 15px;
                padding: 0;
            }
            p{
                font-size: 16px;
                margin-bottom: 10px;
                text-align: justify;
                &:last-child{
                    margin: 0;
                }
                @media (max-width: 1200px){        
                    font-size: 15px;
                    @media (max-width: 992px){
                        font-size: 14px;
                    }
                }
            }
            u{
                font-size: 16px;
                margin-bottom: 10px;
                display: block;
                &:last-child{
                    margin: 0;
                }
                @media (max-width: 992px){
                    font-size: 14px;
                    margin-bottom: 10px;
                }
            }
            ul{
                font-size: 16px;
                list-style: disk;
                padding-left: 15px;
                margin-bottom: 20px;
                @media (max-width: 1200px){        
                    font-size: 15px;
                    @media (max-width: 992px){
                        font-size: 14px;
                        margin-bottom: 10px;
                    }
                }
                li{
                    display: list-item;
                    margin-bottom: 8px;
                    line-height: 1.3;
                    &:last-child{
                        margin: 0;
                    }
                }
            }
        }
        &-button{
            @media (max-width: 767px){
                margin-top: -10px;
            }
        }
    }

    .client-form{
        color: #001f33;
        margin: 70px 0;
        background: #f7f7f7;
        @media (max-width: 1200px){
            margin: 50px 0;
        }
        h3{
            @media (max-width: 992px){
                margin: -30px auto 20px;
                @media (max-width: 560px){
                    width: 100%;
                    font-size: 16px;
                    text-align: center;
                    padding: 15px;
                }
            }
        }

        form{
            @media (min-width: 1200px){
                padding: 0 15px;
            }
            input, textarea{
                width: 100%;
                border: none;
                color: #001f33;
                background: #fff;
                border-radius: 3px;
                padding: 20px 15px;
                font-size: 18px;
                margin-bottom: 15px;
                border: 2px solid #63c1c5;
                @media (max-width: 767px){
                    padding: 15px 10px;
                    font-size: 15px;
                }

                &.btn-bckgrd{
                    width: 300px;
                    margin: 5px auto 20px;
                    font-size: 20px;
                    padding: 17px 0;
                    display: block;
                    text-align: center;
                    color: #fff;
                    transition: .3s;
                    background: #001f33;
                    border: 2px solid #001f33;
                    text-transform: uppercase;
                    position: relative;
                    margin-left: auto;
                    font-weight: 800;
                    &:hover{
                        background-color: #fff;
                        color: #001f33;
                    }
                    @media (max-width: 767px){
                        padding: 13px 45px;
                        width: 100%;
                    }
                }
            }

            textarea{
                height: 200px;
                @media (max-width: 767px){
                    height: 120px;
                }
            }
        }
    }

    &.front-page{
        @media (max-width: 992px){
            .above-footer{
                margin: 0;
            }
        }
    }

    &.company{
        background-color: #fff;
        @media (max-width: 767px){
            .above-footer{
                background-color: #fff;
            }
        }
    }

    &.contacts{
        background-color: #fff;
        .slider{
            @media (max-width: 767px){
                margin-bottom: 15px;
            }
        }
    }

    .contacts{
        &-section{
            margin-bottom: 30px;
            @media (max-width: 1200px){
                margin-bottom: 20px;
                @media (max-width: 767px){
                    margin-bottom: 15px;
                }
            }
        }
        &-main{
            color: #001f33;
            @media (max-width: 1200px){
                padding-right: 10px;
                @media (max-width: 767px){
                    padding-right: 15px;
                }
            }
            .h1{
                @media (max-width: 767px){
                    font-size: 30px;
                }
                &:before{
                    left: 0;
                    opacity: 1;
                    transform: translate(0,0);
                }
            }
            &-info{
                .flex-display(flex);
                .justify-content(space-evenly);
                margin-bottom: 39px;
                @media (max-width: 1200px){
                    margin-bottom: 30px;
                    @media (max-width: 767px){
                        .flex-direction(column);
                        .align-items(center);
                        margin-bottom: 40px;
                    }
                }
                &-block{
                    text-align: center;
                    padding: 20px 0;
                    .flex-direction(column);
                    .flex-display(flex);
                    .justify-content(space-between);
                    @media (max-width: 1200px){
                        padding: 5px 0 20px;
                        @media (max-width: 992px){
                            padding: 5px 0 10px;
                            @media (max-width: 767px){
                                width: max-content;
                                padding: 0;
                            }
                        }
                    }
                    &:nth-child(2){
                        padding: 20px 60px;
                        margin: 0 40px;
                        border-left: 2px solid #e1e1e1;
                        border-right: 2px solid #e1e1e1;
                        @media (max-width: 1200px){
                            padding: 5px 50px 20px;
                            @media (max-width: 992px){
                                padding: 5px 15px 10px;
                                margin: 0 15px;
                                @media (max-width: 767px){
                                    padding: 30px 0;
                                    margin: 30px 0;
                                    border: none;
                                    border-top: 2px solid #e1e1e1;
                                    border-bottom: 2px solid #e1e1e1;
                                }
                            }
                        }
                    }
                    img{
                        display: block;
                        margin: 0 auto 25px;
                    }
                    span{
                        font-size: 16px;
                    }
                }
            }
        }
        &-people-section{
            color: #fff;
            @media (max-width: 1200px){
                .col-sm-6{
                    &:first-child{
                        padding-right: 10px;
                    }
                    &:last-child{
                        padding-left: 10px;
                    }
                    @media (max-width: 767px){
                        &:first-child,
                        &:last-child{
                            padding: 0 15px;
                        }
                    }
                }
            }
            &-block{
                height: 340px;
                .bckg-slider();
                .flex-display(flex);
                .flex-direction(column);
                .justify-content(center);
                background-image: url('../img/ceo.png');
                padding-left: 70px;
                box-shadow: inset 300px 0 100px 0 rgba(0,0,0,.6);
                @media (max-width: 1200px){
                    height: 280px;
                    padding-left: 40px;
                    box-shadow: inset 200px 0 100px 0 rgba(0,0,0,.6);
                    @media (max-width: 767px){
                        height: 180px;
                        padding-left: 30px;
                        margin-bottom: 15px;
                        background-position: top center;
                    }
                }
                .h2{
                    line-height: 1;
                    margin-bottom: 20px;
                    padding: 0;
                    @media (max-width: 1200px){
                        margin-bottom: 15px;
                        @media (max-width: 767px){
                            font-size: 20px;
                        }
                    }
                }
                &-position{
                    font-size: 14px;
                    margin-bottom: 20px;
                    @media (max-width: 1200px){
                        font-size: 13px;
                        margin-bottom: 15px;
                    }
                }
                &-phone,
                &-email{
                    font-size: 16px;
                    margin-bottom: 20px;
                    @media (max-width: 1200px){
                        margin-bottom: 15px;
                        font-size: 15px;
                    }
                }
                &-email{
                    text-decoration: underline; 
                    margin: 0;
                }
                &.director{
                    background-image: url('../img/director.png');
                }
            }
        }
        &-aside{
            @media (max-width: 1200px){
                padding-left: 10px;
                @media (max-width: 767px){
                    padding-left: 15px;
                }
            }
            .gform_wrapper{
                textarea{
                    margin-bottom: 15px;
                    @media (max-width: 1200px){
                        height: 105px;
                        @media (max-width: 992px){
                            margin-bottom: 10px;
                            height: 90px;
                        }
                    }
                }
            }
        }
    }

    .side{
        &-menu{
            &-ul{
                padding: 15px;
                margin-bottom: 20px;
                background: #f7f7f7;
                li{
                    display: block;
                    &:not(:last-child){
                        margin-bottom: 12px;
                        @media (max-width: 1200px){        
                            margin-bottom: 8px;
                        }
                    }
                    &:not(.big){
                        padding-left: 10px;
                        @media (max-width: 1200px){        
                            padding-left: 7px;
                            @media (max-width: 992px){
                                padding-left: 0;
                            }
                        }
                    }
                    a{
                        color: #001f33;
                        font-size: 16px;
                        @media (max-width: 1200px){        
                            font-size: 15px;
                            @media (max-width: 992px){
                                font-size: 14px;
                            }
                        }
                        &:hover{
                            text-decoration: underline;
                        }
                    }
                }
                .big{
                    &:not(:first-child){
                        margin-top: 25px;
                        @media (max-width: 992px){
                            margin-top: 20px;
                        }
                    }
                    a{
                        font-size: 18px;
                        text-transform: uppercase;
                        color: #67b168;
                        @media (max-width: 1200px){        
                            font-size: 16px;
                            text-transform: none;
                            
                        }
                    }
                }
            }
        }
        &-form{
            margin: 0;
            background: none;
            &-container{
                padding: 0;
            }
            form{
                background: #f7f7f7;
                padding: 50px 15px 0;
                display: block;
                margin-top: -30px;
                input, textarea{
                    padding: 15px;
                    font-size: 16px;
                }
                textarea{
                    height: 100px;
                }
                input.btn-bckgrd{
                    width: 100%;
                    margin: 0px auto 20px;
                    font-size: 18px;
                    padding: 15px 0;
                }
            }
            h3{
                margin: 0px auto 0px;
                font-size: 20px;
                padding: 15px;
                width: max-content;
                text-align: center;
                position: relative;
                @media (max-width: 992px){
                    padding: 10px 5px;
                    font-size: 18px;
                    width: 100%;
                }
            }
        }
    }

    .not-front{
        &-title{
            text-align: center;
            padding: 0px;
            margin-bottom: 20px;
            font-size: 30px;
            @media (max-width: 1200px){        
                font-size: 26px;
                @media (max-width: 992px){
                    font-size: 24px;
                }
            }
        }
        &-text{
            p{
                font-size: 16px;
                margin-bottom: 20px;
                text-align: justify;
                &:last-child{
                    margin: 0;
                }
                @media (max-width: 1200px){        
                    font-size: 15px;
                    @media (max-width: 992px){
                        font-size: 14px;
                        margin-bottom: 10px;
                    }
                }
            }
            u{
                font-size: 16px;
                margin-bottom: 10px;
                display: block;
                &:last-child{
                    margin: 0;
                }
                @media (max-width: 992px){
                    font-size: 14px;
                    margin-bottom: 10px;
                }
            }
            ul{
                font-size: 16px;
                list-style: disk;
                padding-left: 15px;
                margin-bottom: 20px;
                @media (max-width: 1200px){        
                    font-size: 15px;
                    @media (max-width: 992px){
                        font-size: 14px;
                        margin-bottom: 10px;
                    }
                }
                li{
                    display: list-item;
                    margin-bottom: 8px;
                    line-height: 1.3;
                    &:last-child{
                        margin: 0;
                    }
                }
            }
        }
        &-block{
            background: #f7f7f7;
            padding: 15px;
        }
    }

    .contacts{
        @media (max-width: 767px){
            padding: 0;
        }
        &-title{
            margin-bottom: 20px;
            @media (max-width: 992px){
                margin-bottom: 10px;
            }
        }
        &-block:not(:last-child){
            margin-bottom: 20px;
            @media (max-width: 992px){
                margin-bottom: 15px;
            }
        }
        span{
            display: block;
            font-size: 18px;
            font-weight: bold;
            margin-bottom: 10px;
            @media (max-width: 1200px){        
                font-size: 16px;
                @media (max-width: 992px){
                    font-size: 15px;
                    margin-bottom: 5px;
                }
            }
        }
        p,a{
            display: block;
            margin-bottom: 10px;
            width: max-content;
            padding-left: 10px;
            font-size: 16px;
            line-height: 1;
            &:last-child{
                margin-bottom: 0;
            }
            &:hover:not(p){
                text-decoration: underline;
            }
            @media (max-width: 1200px){        
                font-size: 15px;
                @media (max-width: 992px){
                    font-size: 14px;
                    margin-bottom: 5px;
                    @media (max-width: 480px){
                        padding: 0;
                    }
                }
            }
        }
    }

    .air-emissions{
    }

    .ovd{
        li{
            b{
                margin-bottom: 10px;
                display: block;
            }
            p{
                margin: 0 0 10px 10px !important;
            }
        }
    }
}

