.footer{
    background-color: #001f33;
    box-shadow: none;
    &.wrapper{
        background-color: #001f33;
        border-top: 3px solid #63c1c5;
    }
    .container{
        .flex-display(flex);
        .align-items(center);
        padding: 35px 0;
        @media (max-width: 1200px){
            padding: 30px 0;
            @media (max-width: 767px){
                padding: 20px 15px 20px;
                .flex-wrap(wrap);
            }
        }
    }
    &-text{
        font-size: 16px;
        color: #fff;
        margin-bottom: 10px;
        &:last-child{
            margin: 0;
        }
        span{
            text-decoration: underline;
        }
        @media (max-width: 992px){
            font-size: 14px;
        }
    }
    &-info{
        text-align: right;
        @media (max-width: 767px){
            order: 2;
        }
    }
    &-logo{
        display: flex;
        .align-items(center);
        .flex-basis(300px);
        margin: -7px auto 0;
        img{
            width: 100%;
            height: auto;
        }
        @media (max-width: 992px){
            .flex-basis(200px);
            @media (max-width: 767px){
                order: 1;
            }
        }
    }
    &-info{
        a{
            display: block;
        }
    }
    &-logo,
    &-info,
    &-copyright{
        @media (max-width: 767px){
            text-align: center;
            margin: 0 auto;
            width: 100%;
            margin-bottom: 30px;            
        }
    }
    &-logo{
        @media (max-width: 767px){
            margin-bottom: 20px;
        }
    }
    &-copyright{
        @media (max-width: 767px){
            order: 3;
            margin-bottom: 0;
        }
    }
}

// Above-footer

.above-footer{
    padding: 30px 0;    
    background-color: #f7f7f7;
    margin: 50px -50px 0 -50px;
    @media (max-width: 1270px){
        margin: 50px 0 0;
        @media (max-width: 1200px){
            margin: 30px 0 0;
            @media (max-width: 767px){
                margin: 0;
                padding: 15px 15px 30px;
            }
        }
    }

    h3{
        margin: -60px auto 20px;
        font-size: 26px;
        font-weight: 700;
        width: max-content;
        padding: 15px 30px;
        background: #63c1c5;
        text-transform: uppercase;
        color: #fff;
        @media (max-width: 992px){
            font-size: 22px;
            margin: -60px auto 25px;
            @media (max-width: 767px){
                font-size: 20px;
                margin: -45px auto 25px;
            }
        }
    }
    
    .container{
        padding: 0;
    }

    &-block{
        @media (max-width: 767px){
            &:last-child{
                a{
                    border-bottom: 2px solid #e1e1e1;
                    &:hover{
                        border-bottom: 2px solid #001f33;
                    }
                }
            }
        }
    }
    
    .slick-track{
        display: flex;
        align-items: center;
    }
    &-link{ 
        display: block;
        position: relative;
        width: 100%;
        height: auto;
        text-align: center;
        background-color: #fff;
        border: 2px solid #e1e1e1;
        text-transform: uppercase;
        color: #001f33;
        transition: 0;
        padding: 95px 5px 10px;
        background-repeat: no-repeat;
        background-position: 50% 20%;
        background-size: auto 70px;
        word-break: break-word;
        @media (max-width: 767px){
            background-position: 50% 15%;
            padding: 90px 5px 10px;
        }

        &:before{
            content: '';
            background-repeat:  no-repeat;
            position: absolute;
            left: 50%;
            top: 20px;
            transform: translate(-50%,0);
        }

        &.kpi{
            background-image: url('../img/kpi.png');
        }
        &.eco-center{
            background-image: url('../img/eco-center.png');
        }
        &.dtek{
            background-image: url('../img/dtek.png');
        }
        &.delicia{
            background-image: url('../img/delicia.png');
        }
        &.temp{
            background-image: url('../img/polution-green.png');
        }
    }
}