.header{
    .container{
        padding: 0;
        .flex-display(flex);
        .align-items(center);
        @media (max-width: 992px){
            .justify-content(space-between);
        }
    }
    &-logo{
        .flex-basis(250px);
        display: flex;
        align-items: center;
        img{
            width: 100%;
            height: auto;
        }
        @media (max-width: 1200px){
            .flex-basis(225px);
            @media (max-width: 767px){
                .flex-basis(180px);
                @media (max-width: 560px){
                    .flex-basis(140px);
                    @media (max-width: 480px){
                        .flex-basis(120px);
                        @media (max-width: 380px){
                            display: none;
                        }
                    }
                }
            }
        }
    }
    &-info{
        text-align: right;
        @media (max-width: 992px){
            margin: 0 auto;
            text-align: center;
            @media (max-width: 767px){
                @media (max-width: 380px){
                    margin: 0 auto 0 0;
                }
            }
        }
        &-text{
            color: #001f33;
            text-transform: uppercase;
            font-size: 20px;
            line-height: 1.1;
            margin-bottom: 5px;
            @media (max-width: 1200px){
                font-size: 19px;
                @media (max-width: 767px){
                    font-size: 17px;
                    @media (max-width: 480px){
                        font-size: 15px;
                    }
                }
            }
        }
        &-phones{
            @media (min-width: 992px){
                margin-bottom: 5px;
            }
            @media (max-width: 992px){
                width: max-content;
                margin: 0 auto;
            }
            a{
                color: #fff;
                font-size: 20px;
                font-weight: 600;
                line-height: 1.3;
                display: block;
                position: relative;
                width: max-content;
                margin-left: auto;
                &:first-child{
                    margin-bottom: 2px;
                }
                &:after{
                    content: '';
                    width: 0;
                    height: 2px;
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    background: #fff;
                    transition: .3s;
                }
                &:hover{
                    &:after{
                        width: 100%;
                    }
                }
                @media (max-width: 1200px){
                    font-size: 19px;
                    @media (max-width: 767px){
                        font-size: 17px;
                        @media (max-width: 480px){
                            font-size: 15px;
                        }
                    }
                }
            }
        }
        &-mail{
            a{
                color: #fff;
                font-size: 13px;
                text-transform: uppercase;
                position: relative;
                margin-left: auto;
                font-weight: 800;
                &:after{
                    content: '';
                    width: 0;
                    height: 2px;
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    background: #fff;
                    transition: .3s;
                }
                &:hover{
                    &:after{
                        width: 100%;
                    }
                }
                @media (max-width: 1200px){
                    font-size: 12px;
                    @media (max-width: 767px){
                        font-size: 11px;
                    } 
                }               
            }
        }
    }
    &-burger{
        padding: 0;
        width: 40px;
        @media (min-width: 992px){
            display: none;
        }
        @media (max-width: 767px){
            width: 30px;
        } 
        .icon-bar{
            width: 100%;
            height: 3px;
            background-color: #fff; 
            margin-bottom: 4px;
            display: block;
            transition: .3s;
            &:last-child{
                margin: 0;
            } 
        }
        &.open{
            .icon-bar{
                transform-origin: center;
                position: relative;
                transition: .3s;
                &.icon-first{
                    transform: rotate(45deg);
                    top: 7px;
                }
                &.icon-second{
                    opacity: 0;
                }
                &.icon-third{
                    transform: rotate(-45deg);
                    top: -7px;
                }
            }
        }
    }
    &-menu{
        .flex-basis(0);
        .flex-grow(1);
        .flex-shrink(1);
        width: 100%;
        margin: 15px 55px 0 55px;
        @media (max-width: 1200px){
            margin: 15px 35px 0 35px;
            @media (max-width: 992px){
                display: none;
                margin: 0;
                width: 100%;
                position: absolute;
                z-index: 999;
                top: calc(~"100% + 3px");
                left: 0;
                background-color: #001f33;
                box-shadow: inset 0px 0px 10px 0px rgba(0,0,0,.1);
            }
        }
        @media (min-width: 992px){
            display: block !important;
        }
        ul{
            font-size: 0;
            @media (min-width: 992px){
                height: 100%;
                .flex-display(flex);
                .justify-content(space-between);
            }
            @media (max-width: 992px){
                // width: 750px;
                // margin: 0 auto;
            }
        }
        .top-menu > li{
            &:before{
                content: '';
                width: 0%;
                height: 2px;
                position: absolute;
                bottom: -5px;
                background: #fff;
                transition: .3s;
            }
            &:hover:not(.sub-menu){
                @media (min-width: 992px){
                    &:before{
                        content: '';
                        width: 100%;
                    }
                }
            }
        }
        li{
            padding: 0;
            position: relative;
            @media (max-width: 992px){
                display: block;
                border-bottom: 1px solid #63c1c5;
                &:hover{
                    background-color: #001f33;
                    & > a{
                        color: #fff !important;
                    }
                }
            }
            &:last-child{
                @media (max-width: 992px){
                    a{
                        border-bottom: 0;
                    }
                }   
            }
            a{
                color: #fff;
                font-size: 22px;
                transition: .3s;
                @media (min-width: 992px){
                    height: 100%;
                    .flex-display(flex);
                    .align-items(center);
                }
                @media (max-width: 1200px){
                    font-size: 19px;
                    @media (max-width: 992px){
                        width: 750px;
                        margin: 0 auto;
                        font-size: 18px;
                        padding: 15px 0;
                        display: block;
                        @media (max-width: 767px){
                            width: 100%;
                            padding-left: 15px;
                        }
                    }
                }
            }
            &.sub-menu{
                @media (min-width: 992px){
                    &:after{
                        content: '';
                        display: block;
                        position: absolute;
                        right: -13px;
                        top: 47%;
                        border-width: 4px;
                        border-style: solid;
                        border-color: #fff transparent transparent;
                    }
                    &:hover{
                        .sub-menu-ul{
                            display: block !important;
                            height: auto !important;
                        }
                    }
                }
                @media (max-width: 992px){
                    & > a{
                        position: relative;
                        &:after{
                            content: '';
                            display: block;
                            position: absolute;
                            right: 0px;
                            top: 47%;
                            border-width: 6px;
                            border-style: solid;
                            border-color: #fff transparent transparent;
                            @media (max-width: 767px){
                                right: 15px;
                            }
                        }
                        &:hover{
                            &:after{
                                border-color: #fff transparent transparent;
                            }
                        }
                    }
                    .sub-menu-ul{
                        display: none;
                        border-left: 4px solid #001f33;
                        background-color: #f7f7f7;
                        li{
                            border: none;
                            &.big{
                                a{
                                    color: #000;
                                    font-size: 18px;
                                    padding-left: 0px;
                                }
                            }
                            a{
                                color: #000;
                                font-size: 15px;
                                padding: 8px 0 8px 15px;
                            }
                            @media (max-width: 767px){
                                padding-left: 15px;
                            }
                        }
                    }
                }
                &-open{
                    border: none;
                    @media (max-width: 992px){
                        background-color: #001f33;
                        & > a{
                            color: #fff;
                            &:after{
                                border-color: #fff transparent transparent;
                            }
                        }
                    }
                }
            }
            .sub-menu-div{
                @media (min-width: 992px){
                    padding-top: 20px;
                    position: absolute;
                    right: 50%;
                    transform: translate(50%, 0%);
                    z-index: 1;
                }
            }
            .sub-menu-ul{
                @media (min-width: 992px){
                    width: max-content;
                    padding: 12px 10px;
                    display: none !important;
                    background-color: #fff;
                    height: auto;
                    box-shadow: @main-shadow;
                    z-index: 999;
                    position: relative;
                    border: 3px solid #63c1c5;
                    li{
                        margin: 0 0 10px 10px;
                        display: block;
                        &.big{
                            margin-left: 0;
                            a{
                                color: #000;
                                font-size: 16px;
                                text-transform: uppercase;
                                padding: 0;
                            }
                            &:first-child{
                                margin-top: 0;
                            }
                        }
                        &:last-child{
                            margin-bottom: 0;
                        }
                        &:before{
                            bottom: -2px;
                        }
                        a{
                            font-size: 14px;
                            color: #000;
                            display: inline-block;
                            position: relative;
                            &:after{
                                content: '';
                                width: 0%;
                                height: 2px;
                                position: absolute;
                                transition: .3s;
                                bottom: -3px;
                                left: 0;
                                background: #63c1c5;
                            }
                            &:hover{
                                &:after{
                                    content: '';
                                    width: 100%;
                                }   
                            }
                        }
                    }
                    &:before{
                        content: '';
                        display: block;
                        position: absolute;
                        left: 50%;
                        top: -3px;
                        transform: translate(-50%, -100%);
                        border-width: 6px;
                        border-style: solid;
                        border-color: transparent transparent #63c1c5 transparent;
                    }
                }
            }
        }
    }
}