h1,h2,.h1,.h2{
    margin-bottom: 30px;
    position: relative;
    line-height: 1.1;
    @media (max-width: 1200px){
        margin-bottom: 25px;
        @media (max-width: 767px){
            line-height: 1.3;
            margin-bottom: 15px;
        }
    }
}
h1,.h1{
    font-size: 50px;
    padding-bottom: 25px;

    &:before{
        .before-stick();
        left: 50%;
        transform: translate(-50%, 0);
    }
    @media (max-width: 1200px){
        font-size: 40px;
        @media (max-width: 767px){
            font-size: 24px;
            padding-bottom: 15px;
        }
    }
}
h2,.h2{
    font-size: 32px;
    @media (max-width: 1200px){
        font-size: 26px;
        @media (max-width: 767px){
            font-size: 24px;
        }
    }
}
h3,h4,h5,.h3,.h4,.h5{
    font-size: 22px;
    margin-bottom: 15px;
    line-height: 1.1;
    @media (max-width: 1200px){
        font-size: 20px;
        @media (max-width: 767px){
            font-size: 15px;
            margin-bottom: 5px;
            line-height: 1.3;
        }
    }
}

p{
    font-size: 15px;
    line-height: 1.5;
}
.btn-def{
    font-size: 15px;
    width: 230px;
    padding: 17px 0;
    display: inline-block;
    text-align: center;
    color: #fff;
    transition: .3s;
    @media (max-width: 767px){
        width: 100%;
    }
}

.btn-white{
    border: 2px solid #fff;
    &:hover{
        background-color: #fff;
        color: #001f33;
    }
}

.btn-blue, .gform_button{
    background-color: #001f33;
    &:hover{
        background-color: #6e808e;
    }
}

.hide-text{
    display: none;
}